import type { LoaderArgs } from '@remix-run/cloudflare';
import { json } from '@remix-run/cloudflare';
import { useLoaderData } from '@remix-run/react';
import { ClientFactory } from 'shared/clientFactory';
import { ResumesApi } from 'shared/v2/resumes';

import { Box } from '~/components/common/Box';
import ResumeSearchCard from '~/components/pages/Search/Resumes/Resume';

export const loader = async ({ params, context }: LoaderArgs) => {
  const clientResume = ClientFactory.createClient(ResumesApi, context.session);

  const resumeId = params.id;

  const [
    genders,
    specializations,
    location,
    employment,
    workfeatures,
    educationLevel,
    requirementsLevel,
    yearsOfExperience,
    languages,
    languageLevel,
    skills,
    drivingLicenses,
    payoutPeriods,
    resume,
  ] = await Promise.all([
    context.references.gendersMap,
    context.references.specializationsMap,
    context.references.locationMap,
    context.references.employmentsMap,
    context.references.workFeaturesMap,
    context.references.educationLevelMap,
    context.references.requirementsLevelMap,
    context.references.yearsOfExperienceMap,
    context.references.languagesMap,
    context.references.languageLevelMap,
    context.references.skillsMap,
    context.references.drivingLicensesMap,
    context.references.payoutPeriodsMap,
    clientResume.getResume({ id: resumeId || '' }),
  ]);

  const dictionaries = {
    genders: genders.ToArray(),
    specializations: specializations.ToArray(),
    location: location.ToArray(),
    employment: employment.ToArray(),
    workfeatures: workfeatures.ToArray(),
    educationLevel: educationLevel.ToArray(),
    requirementsLevel: requirementsLevel.ToArray(),
    yearsOfExperience: yearsOfExperience.ToArray(),
    languages: languages.ToArray(),
    languageLevel: languageLevel.ToArray(),
    skills: skills.ToArray(),
    drivingLicenses: drivingLicenses.ToArray(),
    payoutPeriods: payoutPeriods.ToArray(),
  };

  return json({ resume, dictionaries });
};

export default function Index() {
  const request = useLoaderData<typeof loader>();
  const { dictionaries } = request;

  const resume = request?.resume;

  return (
    <Box width='100%' maxWidth='920px'>
      <ResumeSearchCard resume={resume} dictionaries={dictionaries} />
    </Box>
  );
}
